<template>
  <div id="app">
     <table>
        <thead>
          <tr >
            <th v-for='col in column' class='title'>
              {{col.name}}
            </th>
            <th class='title'>
            Delete  
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for='(person,index) in people'>
            <td v-for='col in column'>
              <!-- {{person[col.name]}} -->
              <span v-if='col.isKey' v-on:click='openEdit(index)'><a href="javascript:void(0)">{{person[col.name]}}</a></span>
              <span v-else>{{person[col.name]}}</span>
            </td>
            <td>
              <button v-on:click='del(index)'>Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
      <button v-on:click='creatItem'>Creat</button>
    <dialogs v-bind:show='showDialog' v-bind:columns='column' v-bind:title='title' v-bind:mode='mode'></dialogs>
  </div>
</template>

<script>
import dialogs from './components/dialog'   //引用才能在组件中使用相应的标签

export default {
  name: 'app',              			//组件的名字
  components: {							//组件里面有
    dialogs
  },
    data:function () {                //组件数据，必须是function
        return{
            column:[{name:'姓名',isKey:true},{name:'年龄'},{name:'性别',dataSource: ['男', '女']},{name:'籍贯'},{name:'专业'}],
            people:[ {姓名:'张三',年龄:'25',性别:'男',籍贯:'浙江',专业:'电子'},
                     {姓名:'李四',年龄:'27', 性别:'男',籍贯:'上海',专业:'生物'}],
            // people:storeFetch(),
            showDialog:false,   //控制输入框显示
            mode:0,             //mode = 1是新增数据模式，mode = 2是修改数据模式
            title:''            //输入框的title
        }

    },
    methods:{
        del:function(index){                    //删除功能，原生js splice函数
            this.people.splice(index,1);
            },
        creatItem:function(){                   //弹出输入框
                // this.shows=true;
            this.showDialog=true;
            this.mode=1;
            this.title='Creat New Item'
            },
        openEdit:function (index) {             //弹出数据编辑框
            this.showDialog=true;
            this.mode=2;
            this.title='Edit Item';
            this.$children[0].item=JSON.parse(JSON.stringify(this.people[index]));   //用js原生的json序列化的方式深度复制
            this.$children[0].index=index;
        }     
    }

  
}
</script>

<style>
*{
	margin: 0;
	padding: 0;
}
body{
	display: flex;
	align-items: center;
	justify-content: center;
}
table{
	border-collapse: collapse;
	text-align: center;

	/*vertical-align: center;*/
}
td,th {
	border: 1px solid #bcbcbc;
	padding: 5px 10px;
	width: 120px;
	height: 50px;
	
}
#app{
	margin-top: 100px;
	position: relative;
}
.title{
	padding: 10px;
	height: 50px;
	color: white;
	background-color: #0090d3;
	text-align: center;
	line-height: 50px;
	margin-bottom: 25px;
}
button{
	background-color:#0090d3;
	color:white;
	border-radius:10px;
	width: 80px;
	height: 40px;
	outline: none;
}
.dialog{
	width: 500px;
	height: 300px;
	position: absolute;
	top: -35px;
	left: 20%;
	background-color: white;
	border:1px solid #bcbcbc;
	border-radius: 10px;
	z-index: 50;
}

.content,.footer{
	text-align:center;
	margin-top: 10px;
}

.content label{
	padding-right: 1rem;
	
}
.content select{
	width: 173px;
	text-align: center;
}
.footer button{
	margin:0 16px;
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    z-index: 20;
}
</style>



